.initial-content {
  width: 100%;
  height: 100%;
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
}

.col-md-12 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.initial-content .logo {
  margin-top: 200px;
  box-shadow: none !important;
}

.initial-content div {
  margin-top: 10px;
}

.initial-content .btn {
  position: relative;
  min-width: 250px;
  min-height: 50px;
  margin: 10px;
  border-radius: 50px;
  text-align: center;
  padding: 10px;
  font-weight: 500;
  font-size: 1.3em;
}

.initial-content .btn-primary {
  background-color: #4bb3cc;
  border-color: #4bb3cc;
  box-shadow: 0 0 5px 0 gray;
}

.initial-content .btn-success {
  background-color: #27a23e;
  border-color: #27a23e;
  box-shadow: 0 0 5px 0 gray;
}

.initial-content .info-button {
  min-width: 30px;
  min-height: 30px;
  font-size: 0.7em;
  border-radius: 50%;
  padding: 0;
  font-weight: 500;
}

.description-content {
  max-width: 600px;
  border: 1px solid lightgray;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0px 0px 5px 0px lightgray !important;
}

.footer-copyright a {
  color: #4bb3cc !important;
  position: fixed;
  right: 0;
  bottom: 0;
  margin: 20px;
}

.footer {
  position: fixed;
  right: 0;
  bottom: 0;
  margin-right: 20px;
}

.footer .footer-content a {
  color: #4bb3cc !important;
}

.bg-light {
  background-color: white !important;
}

.sidebar {
  background-color: #4bb3cc;
  border-bottom: 3px solid #4bb3cc;
}

.navbar-nav a {
  color: black;
  margin: 10px;
  font-size: 1.3em;
}

.nav a {
  color: white;
  font-size: 1.2em;
  font-weight: 400;
  padding: 10px;
  border: 1px solid #4bb3cc;
  box-shadow: 0px 0px 1px 0px lightgray !important;
  background-color: #4bb3cc;
  flex: 1;
  max-height: 50px;
}

.nav a.active {
  background-color: #2d889e;
  /* filter: brightness(0.9); */
  color: white;
}

.nav a:hover {
  background-color: #2d889e;
  color: white;
}

.nav a:hover {
  text-decoration: none;
}

.navbar-nav a.active {
  border-bottom: 3px solid #4bb3cc;
}

.navbar-nav a:hover {
  font-weight: 500;
  text-decoration: none;
}

.navbar-nav a:active {
  outline: none;
}

.navbar-expand {
  justify-content: flex-end;
  padding-left: 0;
}

.navbar {
  padding: 0 !important;
}

.container-fluid {
  padding: 0;
  margin: 0;
}

.container-fluid .col-md-10 {
  height: 100%;
}

.container-fluid .col-md-9 {
  height: 100%;
}

.topbar,
.topbar div,
.topbar div div {
  margin: 0;
  padding: 0;
}

.logo {
  width: 100%;
  height: 50px;
  color: white;
  padding-left: 10px;
  padding-top: 10px;
  font-size: 1.2em;
}

.photo-div {
  width: 250px;
  height: 300px;
  border: 2px solid #4bb3cc;
  box-shadow: 0 0 5px 0 lightgray;
  background-color: white;
  padding: 70px;
  border-radius: 5px;
}

.photo-div-container>img {
  width: 250px;
  height: 300px;
  border: 2px solid #4bb3cc;
  box-shadow: 0 0 5px 0 lightgray;
  border-radius: 5px;
}

.photo-div-container>svg {
  position: absolute;
  color: #fff;
  width: 30px;
  height: 30px;
  right: 32%;
  margin: 6px;
  cursor: pointer;
}

.photo-div-button {
  display: flex;
  justify-content: center;
  align-items: center;
}

.add-button {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 1em;
  line-height: 1em;
  font-weight: 500;
  color: white;
  box-shadow: 0 0 5px 0 lightgray;
}

.small-button {
  width: 40px;
  height: 40px;
  margin-left: 0 !important;
  font-size: 1em;
  line-height: 1em;
  font-weight: 500;
}

.small-button.no-button {
  width: 10px;
  height: 10px;
  margin: 10px !important;
  margin-top: 0px !important;
  padding-top: 0px !important;
  margin-left: 0px !important;
  padding: 10px !important;
  border: 0;
  box-shadow: none;
  border-radius: 0;
  background: transparent;
  position: absolute;
  left: 94%;
  top: 0%;
}

.small-button.no-button:not(:disabled):not(.disabled):active {
  background-color: transparent !important;
  border-color: transparent !important;
  outline: none !important;
  box-shadow: none !important;
  /* box-shadow: 0 0 0 0.2rem #4bb3cc; */
}

.small-button.no-button:not(:disabled):not(.disabled):active:focus {
  background-color: transparent !important;
  border-color: transparent !important;
  box-shadow: none !important;
  outline: none !important;
  /* box-shadow: 0 0 0 0.2rem #4bb3cc; */
}

.small-button.no-button:hover {
  background: transparent;
}

.small-button .fa-plus {
  height: 20px;
  width: 1em !important;
}

.no-button .fa-plus {
  color: #4bb3cc !important;
}

.fa-plus {
  color: white !important;
  height: 30px;
  width: 1.4em !important;
}

.fa-minus {
  background-color: #dc3545 !important;
  color: #dc3545;
  width: 1em !important;
  height: 5px !important;
}

.fa-map-marker-alt {
  color: #4bb3cc !important;
  margin-right: 5px;
}

.main-content-row {
  align-items: flex-start !important;
}

.instant-add-wrapper {
  display: flex;
  justify-content: flex-end;
}

.instant-add-wrapper .add-button {
  margin: 0 !important;
  padding: 0 !important;
  background-color: transparent !important;
  border-color: transparent !important;
  box-shadow: none !important;
  outline: none !important;
  width: 10px;
  height: 10px;
}

.instant-add-wrapper .add-button .fa-plus {
  color: #4bb3cc !important;
}

.shift-left {
  padding-left: 3px !important;
}

.row-cards {
  justify-content: flex-start;
  align-items: flex-start;
}

.button-label p {
  font-size: 1.2em;
  margin-bottom: 0;
}

.button-label {
  display: flex;
  justify-content: center;
  align-items: center;
}

.side-button-wrapper {
  display: flex;
  justify-content: flex-end;
}

.languagebox {
  width: 70px !important;
  border-radius: 5px !important;
  border: 2px solid #4bb3cc !important;

}

.languagebox>input {
  text-transform: uppercase !important;
  padding-top: 0px;
  padding-left: 5px;
  color: black;
  font-weight: 400;
  font-size: 1.3em;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.rw-list-empty,
.rw-list-option,
.rw-list-optgroup {
  padding: 1px 1px !important;
}

.rw-btn.rw-btn-select {
  padding-bottom: 4px !important;
}

.rw-list-option>span {
  padding-left: 5px !important;
}

/* .rw-combobox.rw-widget {
  width: auto !important;
} */

.languagebox.submit-window {
  height: 46px;
}

.error {
  color: white;
  background-color: red;
  border-radius: 2px;
  padding: 3px;
  font-size: 10px;
}

.fa-book-open {
  color: #4bb3cc !important;
}

.fa-landmark {
  color: #4bb3cc !important;
}

.fa-pencil-alt {
  color: #4bb3cc !important;
}

.fa-copyright {
  color: #4bb3cc !important;
  margin-right: 5px;
  margin-top: 4px;
}

/******************************************** 
********************* CARD ******************
*********************************************
*********************************************/

.list-group-item.other-card {
  border: 0;
  padding: 0;
  margin-bottom: 10px;
}

.list-group-item.other-card .card {
  margin: 0;
  border: 0;
  border-radius: 5px;
}

.list-group-item.other-card .card .card-header {
  padding-bottom: 0.2em;
}

.list-group-item.other-card .card .card-body {
  padding-top: 0;
  padding-bottom: 1em;
}

.modal-body .rw-combobox {
  display: flex;
  justify-content: center;
}